
import errorMp3 from './error.mp3'
import coin from './coin.mp3'

const UIfx = typeof window !== `document` ? require('uifx') : null;

export const errBeep = typeof window !== `document` ? new UIfx(errorMp3, {
  volume: 0.3, // number between 0.0 ~ 1.0
  throttleMs: 100
}) : null;

export const coinCollect = typeof window !== `document` ? new UIfx(coin, {
  volume: 0.3, // number between 0.0 ~ 1.0
  throttleMs: 100
}) : null;